<template>
  <div>
    <div class="bottom">
      <div class="w1200 font14">
        <div class="left fl">
          <p class="fl">
            {{$t('bottom.serviceNode')}}:
            <u class="click" @click="toUrl('nodeService')">{{$store.getters.getCurrentUrlData.urls}}</u>
          </p>
        </div>
        <div class="right fr">
          {{$t('bottom.nodeHeight')}}:
          <i v-show="heightInfo.networkHeight ===0 && heightInfo.localHeight===0 " class="el-icon-loading"></i>
          <span v-show="heightInfo.networkHeight !==0 && heightInfo.localHeight !==0 ">
            {{heightInfo.networkHeight}}/{{heightInfo.localHeight}}
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import nerve from 'nerve-sdk-js'
  import {divisionDecimals} from '@/api/util'

  export default {
    name: "bottom-bar",
    data() {
      return {
        heightInfo: [],//
        failedNu: 0,//
      }
    },
    created() {
      this.getHeaderInfo();
    },
    mounted() {
      setInterval(() => {
        this.getHeaderInfo();
        this.getAddressInfo();
      }, 10000);
    },
    watch: {
      //
      heightInfo(val) {
        if (this.$route.path !== '/nodeService' && this.failedNu !== 5) {
          if (val.localHeight === 0 && val.networkHeight === 0) {
            this.failedNu = this.failedNu + 1
          } else {
            this.failedNu = 0
          }
          if (this.failedNu === 5) {
            this.$confirm(this.$t('bottom.err0'), {
              confirmButtonText: this.$t('bottom.err1'),
              cancelButtonText: '',
              type: 'error',
              showClose: false,
              showCancelButton: false,
              closeOnClickModal: false,
              closeOnPressEscape: false,
            }).then(() => {
              this.toUrl('nodeService');
            }).catch(() => {
            });
          }
        }
      }
    },
    methods: {

      /**
       * 
       */
      getHeaderInfo() {
        /*
        const url = this.$store.getters.getCurrentUrlData.urls;
        const params = {
          "jsonrpc": "2.0", "method": "getInfo", "params": [chainID()], "id": Math.floor(Math.random() * 1000)
        };
        */
        this.$post('/', 'getInfo', [])
          .then((response) => {
            if (response.hasOwnProperty("result")) {
              this.heightInfo = response.result;
              sessionStorage.setItem("info", JSON.stringify(response.result))
            } else {
              this.heightInfo = {localHeight: 0, networkHeight: 0};
              sessionStorage.removeItem("info")
            }
          })
          .catch((error) => {
            sessionStorage.removeItem("info");
            this.heightInfo = {localHeight: 0, networkHeight: 0};
            console.log("getInfo:" + error)
          })
      },

      /**
       * 
       **/
      async getAddressInfo() {
        let addressInfos = this.$store.getters.getSelectAddress
        let addressList = this.$store.state.addressInfo
        if (addressInfos) {
          await this.$post('/', 'getAccount', [addressInfos.address], 'BottomBar')
            .then((response) => {
              //console.log(response);
              if (response.hasOwnProperty("result")) {
                for (let item of addressList) {
                  if (item.address === addressInfos.address) {
                    item.alias = response.result.alias;
                    item.balance = divisionDecimals(response.result.balance);
                    item.consensusLock = divisionDecimals(response.result.consensusLock);
                    item.totalReward = divisionDecimals(response.result.totalReward);
                    if (response.result.lastReward) {
                      item.lastReward = divisionDecimals(response.result.lastReward);
                    } else {
                      item.lastReward = 0;
                    }
                    item.tokens = [];
                    item.chainId = nerve.verifyAddress(item.address).chainId;
                  }
                }
                this.$store.commit('setAddressInfo', addressList)
              }
            })
        }
      },

      /**
       * 
       * @param name
       */
      toUrl(name) {
        this.$router.push({
          name: name
        })
      },
    }
  }
</script>

<style lang="less">
  @import "./../assets/css/style";

  .bottom {
    height: 60px;
    border-top: 1px solid @Dcolour;
    position: fixed;
    line-height: 60px;
    bottom: 0;
    background-color: @Bcolour;
    width: 100%;
    z-index: 99;
    @media screen and (max-width: 1000px) {
      height: 4rem;
      font-size: 0.7rem;
      line-height: 1rem;
    }
    .w1200 {
      .left {
        width: 50%;
        line-height: 60px;
        @media screen and (max-width: 1000px) {
          width: 100%;
          margin: 0.5rem 0 0 0.5rem;
          p {
            margin: 0;
            float: none;
            display: block;
          }
        }
      }
      .right {
        width: 40%;
        line-height: 60px;
        text-align: right;
        @media screen and (max-width: 1000px) {
          width: 100%;
          float: none;
        }
        label {
          padding: 0 10px;
          @media screen and (max-width: 1000px) {
            padding: 0 0.5rem;
            font-size: 0.7rem;
            line-height: 1rem;
          }
        }
      }
    }

  }
</style>
